import * as React from 'react';
import ButtonPrimary from 'rili-public-library/react-components/ButtonPrimary';
import Input from 'rili-public-library/react-components/Input';
import translator from '../services/translator';

// Regular component props
interface IRegisterFormProps {
  register: Function;
}

interface IRegisterFormState {
    inputs: any;
}

/**
 * RegisterForm
 */
export class RegisterFormComponent extends React.Component<IRegisterFormProps, IRegisterFormState> {
    private translate: Function;

    constructor(props: IRegisterFormProps) {
        super(props);

        this.state = {
            inputs: {},
        };

        this.translate = (key: string, params: any) => translator('en-us', key, params);
    }

    isFormDisabled() {
        return !this.state.inputs.userName || !this.state.inputs.password || !this.isFormValid();
    }

    isFormValid() {
        return this.state.inputs.password === this.state.inputs.repeatPassword
    }

    onSubmit = (event: any) => {
        switch (event.target.id) {
            case 'password':
            case 'user_name':
            case 'register':
                if (!this.isFormDisabled()) {
                    console.log(this.state.inputs);
                    const creds = { ...this.state.inputs };
                    delete creds.repeatPassword;
                    // this.props.register(creds);
                }
        }
    }

    onInputChange = (name: string, value: string) => {
        const newInputChanges = {
            [name]: value,
        };
        this.setState({
            inputs: {
                ...this.state.inputs,
                ...newInputChanges,
            }
        });
    }

    public render(): JSX.Element | null {
        return (
            <div className="register-container">
                <h1 className="text-center">Register</h1>
                <label htmlFor="first_name">First Name:</label>
                <Input type="text" id="first_name" name="firstName" value={this.state.inputs.firstName} onChange={this.onInputChange} onEnter={this.onSubmit} translate={this.translate} />
                
                <label htmlFor="last_name">Last Name:</label>
                <Input type="text" id="last_name" name="lastName" value={this.state.inputs.lastName} onChange={this.onInputChange} onEnter={this.onSubmit} translate={this.translate} />
                
                <label htmlFor="e_mail">E-mail:</label>
                <Input
                    type="text"
                    id="e_mail"
                    name="email"
                    value={this.state.inputs.email}
                    onChange={this.onInputChange}
                    onEnter={this.onSubmit}
                    translate={this.translate}
                    validations={['isRequired']}
                />
            
                <label htmlFor="phone_number">Mobile Phone #:</label>
                <Input type="text" id="phone_number" name="phoneNumber" value={this.state.inputs.phoneNumber} onChange={this.onInputChange} onEnter={this.onSubmit} translate={this.translate} />
                
                <label htmlFor="user_name">Username:</label>
                <Input type="text" id="user_name" name="userName" value={this.state.inputs.userName} onChange={this.onInputChange} onEnter={this.onSubmit} translate={this.translate} />

                <label htmlFor="password">Password:</label>
                <Input type="password" id="password" name="password" value={this.state.inputs.password} onChange={this.onInputChange} onEnter={this.onSubmit} translate={this.translate} />
                
                <label htmlFor="repeat_password">Repeat Password:</label>
                <Input type="password" id="repeat_password" name="repeatPassword" value={this.state.inputs.repeatPassword} onChange={this.onInputChange} onEnter={this.onSubmit} translate={this.translate} />

                <div className="form-field text-right">
                    <ButtonPrimary id="register" text="Register" onClick={this.onSubmit} disabled={this.isFormDisabled()} />
                </div>
            </div>
        );
    }
}

export default RegisterFormComponent;